@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/*Global*/
:root{
  font-family: "Roboto",sans-serif;
}
*{
  box-sizing: border-box;
}
ul{
  list-style: none;
}
.App {
  text-align: center;
}
.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

/* Header */
header{
  min-height: 10vh;
}
header h1{
  font-size: 2rem;
}

/* Main */
main{
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Add Todo */
form{
  margin-top: 20px;
}
.addToInput{
  padding: 10px;
}
.addToButton{
  padding: 10px;
  border:none;
  color: #fff;
  background-color: tomato;
  font-size: 1rem;
  margin-left: 5px;
  cursor: pointer;
}

/* Todo Lists */
.TodoLists{
  width: 300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.TodoList{
  margin-top: 10px;
}

.TodoList h1{
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 10px;
}
.listItem{
  cursor: pointer;
}
.listItem:hover{
  opacity: .6;
}
.checked{
  text-decoration: line-through;
}
.important{
  font-weight: bold;
  color: red;
}
.invisible{
  opacity: 0.2;
}
.itemRemoveButton{
  color: red;
  cursor: pointer;
  border-radius: 50%;
  margin-left: 10px;
  font-size: 1.4rem;
  border: none;
  background-color: transparent;
  padding: 0;
}

/* Remove All */
.removeButton{
  background-color: tomato;
  color: #fff;
  border: none;
  height: 44px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 10px;
  width: 300px;
}

/* Footer */
footer{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 10vh;
  background-color: #282c34;
  color: #fff
}

